export default {
    data: {},
    methods: function (cb) {
        let _self = this;
        axios.get('http://apiv2.chujingyi.cn/v2/home/best_hospital').then((req) => {
            let BPA = [
                {mark: 'AGH', child: [], name: "", nameen: "Authoritative General Hospital"},
                {mark: 'CCH', child: [], name: "", nameen: "Cancer Hospital"},
                {mark: 'CRH', child: [], name: "", nameen: "Children's Hospital"},
                {mark: 'CVH', child: [], name: "", nameen: "Cardiovascular hospital"},
            ];
            let reqdata = req.data.data;
            for (let i = 0; i < reqdata.length; i++) {
                switch (reqdata[i].type_name) {
                    case reqdata[i].type_name = "":
                        BPA[0].child.push(reqdata[i]);
                        break;
                    case reqdata[i].type_name = "":
                        BPA[1].child.push(reqdata[i]);
                        break;
                    case reqdata[i].type_name = "":
                        BPA[2].child.push(reqdata[i]);
                        break;
                    case reqdata[i].type_name = "":
                        BPA[3].child.push(reqdata[i]);
                        break;
                    default:
                }
            }
            _self.data = BPA;
            cb(_self);
        });
    }
};